/* nuosu-sil-yi-400-normal*/
@font-face {
  font-family: 'Nuosu SIL';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url('./files/nuosu-sil-yi-400-normal.woff2') format('woff2'), url('./files/nuosu-sil-all-400-normal.woff') format('woff');
  unicode-range: U+3001-3002, U+3008-3011, U+3014-301B, U+30FB, U+A000-A48C, U+A490-A4C6, U+FF61-FF65;
}
